/* cormorant-garamond-regular - cyrillic_latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: 'Cormorant Garamond';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.eot'); /* IE9 Compat Modes */
  src: url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.woff') format('woff'), /* Modern Browsers */
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../fonts/cormorant-garamond/cormorant-garamond-v16-cyrillic_latin-regular.svg#CormorantGaramond') format('svg'); /* Legacy iOS */
}
